import ProjectDetail from "../../components/ProjectDetail";

interface ProjectPageProps {
  params: { slug: string };
}

export default async function ProjectDetailPage({ params }: ProjectPageProps) {
  console.log("‚úÖ Server params:", params);

  // If slug missing, show simple fallback
  if (!params?.slug) {
    return (
      <main className="p-8">
        <h1 className="text-2xl font-bold text-red-600">Error: Missing slug</h1>
        <p>Try visiting /projects/modern-hospital manually.</p>
      </main>
    );
  }

  return (
    <main>
      <ProjectDetail slug={params.slug} />
    </main>
  );
}
